<template>
	<view class="content">

		<!-- 加载动画 -->
		<u-loading mode="circle" :show="true"></u-loading>

		<view class="header">
			<!-- 用户未登录 -->
			<view class="header_login" v-if="weixinLogin">
				<view class="left">
					<view class="avatar">
						<open-data type="userAvatarUrl"></open-data>
					</view>
					<view class="nickName">
						<open-data type="userNickName"></open-data>
					</view>
				</view>
				<view class="right">
					<text>已登录</text>
				</view>
				<button type="default" @tap="findDaily">查询用户日记</button>
			</view>
			<!-- 登录后 -->
			<view class="header_login" v-else>
				<view class="left">
					<view class="avatar">
						<open-data type="userAvatarUrl"></open-data>
					</view>
					<view class="nickName">
						<text>匿名用户</text>
					</view>
				</view>
				<view class="right">
					<button type="default" @tap="loginByWeixin">微信授权</button>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	let weixinAuthService
	export default {
		data() {
			return {
				// uni-id自带，默认为false
				hasWeixinAuth: false,
				// 判断是否已经微信登录，默认为false
				weixinLogin: false
			}
		},
		onLoad() {

		},
		methods: {
			getWeixinCode() {
				return new Promise((resolve, reject) => {
					// #ifdef APP-PLUS
					weixinAuthService.authorize(function(res) {
						resolve(res.code)
					}, function(err) {
						console.log(err)
						reject(new Error('微信登录失败'))
					});
					// #endif
					// #ifdef MP-WEIXIN
					uni.login({
						provider: 'weixin',
						success(res) {
							resolve(res.code)
						},
						fail(err) {
							reject(new Error('微信登录失败'))
						}
					})
					// #endif
				})
			},
			code2SessionWeixin() {
				this.getWeixinCode().then((code) => {
					return uniCloud.callFunction({
						name: 'uni-id-test',
						data: {
							action: 'code2SessionWeixin',
							params: {
								code,
							}
						}
					})
				}).then((res) => {
					uni.showModal({
						showCancel: false,
						content: JSON.stringify(res.result)
					})
				}).catch((e) => {
					console.error(e)
					uni.showModal({
						showCancel: false,
						content: '微信登录失败，请稍后再试'
					})
				})
			},
			unbindWeixin() {
				uniCloud.callFunction({
					name: 'uni-id-test',
					data: {
						action: 'unbindWeixin'
					},
					success(res) {
						uni.showModal({
							showCancel: false,
							content: JSON.stringify(res.result)
						})
					},
					fail(e) {
						console.error(e)
						uni.showModal({
							showCancel: false,
							content: '微信解绑失败，请稍后再试'
						})
					}
				})
			},
			bindWeixin() {
				this.getWeixinCode().then((code) => {
					return uniCloud.callFunction({
						name: 'uni-id-test',
						data: {
							action: 'bindWeixin',
							params: {
								code: code,
							}
						}
					})
				}).then((res) => {
					uni.showModal({
						showCancel: false,
						content: JSON.stringify(res.result)
					})
				}).catch(() => {
					uni.showModal({
						showCancel: false,
						content: '微信绑定失败，请稍后再试'
					})
				})
			},
			loginByWeixin() {
				uni.showLoading({
				    title: '登录中'
				});
				this.getWeixinCode().then((code) => {
					return uniCloud.callFunction({
						name: 'uni-id-test',
						data: {
							action: 'loginByWeixin',
							params: {
								code,
							}
						}
					})
				}).then((res) => {
					if (res.result.code === 0) {
						this.weixinLogin = true;
						uni.setStorageSync('uni_id_token', res.result.token);
						uni.setStorageSync('uni_id_token_expired', res.result.tokenExpired);
						uni.setStorageSync('uid', res.result.uid);
						uni.hideLoading();
					}
				}).catch((e) => {
					uni.showModal({
						showCancel: false,
						content: '微信登录失败，请稍后再试'
					})
				})
			},


			findDaily() {
				console.log(this.$api);
				let uid = uni.getStorageSync('uid');
				this.$api.get_Info({uid}).then((res) => {
					console.log(res);
				})
			},

			// 验证token
			isLoginIn() {
				return new Promise((resolve, reject) => {
					uni.getStorage({
						key: 'uni_id_token',
						success: function(res) {
							resolve(res);
						}
					})
				}).catch(e => {});
			}
		},

		async onShow() {
			let _this = this;
			let token = await this.isLoginIn();
			if (token) {
				_this.weixinLogin = true;
			}
		}
	}
</script>

<style lang="scss">
	.header {
		height: 200rpx;
		box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
		border-radius: 20rpx;
		margin-top: 25%;
		margin-bottom: 40rpx;
		display: flex;
		justify-content: space-between;
		margin-left: 5%;
		margin-right: 5%;
		display: flex;
		// justify-content: center;
		align-items: center;
		padding: 0 5%;


		.left {
			.avatar {
				height: 100rpx;
				width: 100rpx;
			}
		}

	}
</style>
